import React from "react";
import postMessageToParent from "./postMessageToParent";
import "./styles.css";

export default function CVV() {
  const handleChange = (change) => {
    const { value } = change.target;

    postMessageToParent({
      from: "cvv",
      messageType: "validation",
      data: {
        valid: /^[0-9]{4,}$/.test(value),
      },
    });
  };

  const handleFocus = () => {
    postMessageToParent({
      from: "cvv",
      messageType: "event",
      data: {
        eventName: "onfocus",
        value: true,
      },
    });
  };

  const handleBlur = () => {
    postMessageToParent({
      from: "cvv",
      messageType: "event",
      data: {
        eventName: "onblur",
        value: true,
      },
    });
  };

  return (
    <input
      type="text"
      placeholder="CVV"
      name="cvv"
      onChange={handleChange}
      onFocus={handleFocus}
      onBlur={handleBlur}
    />
  );
}
